<div >
    <div class="ma-5" >
        <v-window 
            v-model="selected_page" 
            :style="{
                'border': 'solid',
                'background-color': '#979797',
                'height': nav_hover ? '50vh' : '90vh',
                'transition': 'height 1s',
                'overflow-y': 'auto'
            }" 
            >
            <v-window-item
                v-for="(page, index) in pages"
                :key="index"    
                :value="page.name"
                >

                <component :is="page.component"></component>

            </v-window-item>
        </v-window>
        <div
            width="100%"
            class="nav-window"
            :style="{
                'height': nav_hover ? '50vh' : '10vh'
            }"
            @mouseenter="nav_hover = true"
            @mouseleave="nav_hover = false"
            >
            <div 
                class="d-flex justify-center"
                :style="{
                    'font-size': '300%',
                    'opacity': nav_hover ? 0 : 1,
                    'overflow': 'hidden',
                    'height':  nav_hover ? '0px' : '250px',
                    // 'transition-delay': '1000ms',
                    // 'transition-property': 'max-height',
                    'transition': 'height 1s, opacity 1s'
                }" >
                Menu's
            </div>
            <div>
                <div id="navlinks">
                    
                    <div class="nav-links">
                        <div @click="clickPage('home')" id="nav-link1">
                            <h2 class="nav-link-title">Home</h2>
                            <img :src="require('@/assets/startphotoai.jpg')" alt="" class="nav-link-img" />
                            <p class="nav-link-p-1">De home pagina</p>
                        </div>
                        
                        <div @click="clickPage('hoe')" id="nav-link2">
                            <h2 class="nav-link-title">Hoe Ai</h2>
                            <img :src="require('@/assets/boxcat.jpg')" alt="" class="nav-link-img" />
                            <p class="nav-link-p-2">Hoe werkt AI</p>
                        </div>
                        <div @click="clickPage('voorbeelden')"  id="nav-link3">
                            <h2 class="nav-link-title">voorbeelden</h2>
                            <img :src="require('@/assets/aivoorbeelden.jpg')" alt="" class="nav-link-img" />
                            <p class="nav-link-p-3">Voorbeelden van AI</p>
                        </div>
                        <div @click="clickPage('waarom')" id="nav-link4">
                            <h2 ckass="nav-link-title">Waarom Ai</h2>
                            <img :src="require('@/assets/waaromai.jpg')" alt="" class="nav-link-img" />
                            <p class="nav-link-p-4">Waarom gebruik je AI</p>
                        </div>
                        <div div @click="clickPage('wanneer')" id="nav-link5">
                            <h2 class="nav-link-title">Wanneer Ai</h2>
                            <img :src="require('@/assets/design.png')" alt="" class="nav-link-img" />
                            <p class="nav-link-p-5">Wanneer gebruik je AI</p>
                        </div>
                        <div @click="clickPage('wat')" id="nav-link6">
                            <h2 class="nav-link-title">Wat is Ai</h2>
                            <img :src="require('@/assets/design.png')" alt="" class="nav-link-img" />
                            <p class="nav-link-p-6">Wat is AI</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>


    </div>
    

    
</div>

</template>

<script>
import { defineComponent } from 'vue';

import HomePage from '@/components/Home'
import WatAIPage from '@/components/WatAIPage'
import WaaromAIPage from '@/components/WaaromAIPage'
import WanneerAIPage from '@/components/WanneerAIPage'
import HoeAIPage from '@/components/HoeAIPage'
import VoorbeeldenPage from '@/components/VoorbeeldenPage'


export default defineComponent({
    name: 'Home',
    components: {
        HomePage,
        WatAIPage,
        WaaromAIPage,
        HoeAIPage,
        VoorbeeldenPage,
        WanneerAIPage
    },
    data(){
        return {
            nav_hover: false,
            pages: [
                {
                    name: 'home',
                    component: 'HomePage'
                },
                {
                    name: 'wat',
                    component: 'WatAIPage'

                },
                {
                    name: 'waarom',
                    component: 'WaaromAIPage'
                },
                {
                    name: 'wanneer',
                    component: 'WanneerAIPage'
                },
                {
                    name: 'hoe',
                    component: 'HoeAIPage'
                },
                {
                    name: 'voorbeelden',
                    component: 'VoorbeeldenPage'
                },

            ],
            selected_page: ''
        }
    },
    computed: {
        pageName(){
            return this.$route.params.page_name
        }
    },
    methods: {
        checkUrl(){
            if (!this.pages.map(e => e.name).includes(this.pageName)){
                console.log('wrong page', this.pages.map(e => e.name), this.pageName)
                this.$router.push('/')
            } else {
                this.selected_page = this.pageName
            }
        },
        clickPage(page_name){
            this.$router.push(page_name)
            this.selected_page = page_name
        },
        showNav(){
            document.body.dataset.navactive = document.body.dataset.navactive === "true" ? "false" : "true";
        }
    },
    created() {
        this.checkUrl()
    },
    watch: {
        pageName(){
            this.checkUrl()
        },
        nav_hover (){
            document.body.dataset.navactive = this.nav_hover
            
        }
    }
});
</script>

