<template>
<v-card>
    <v-card-title :color="colors[selectedSlideIndex]">
        {{slides[selectedSlideIndex]}}
    </v-card-title>
    <v-card-text>
        <v-carousel
            hide-delimiter-background
            :show-arrows="false"
            delimiter-icon="mdi-square"
            v-model="selected_slide"
            >
            <v-carousel-item
                v-for="(slide, i) in slides"
                :key="i"
                :value="slide"
                >
                <v-sheet
                    :color="colors[i]"
                    height="100%"
                    tile
                >
                <div class="d-flex fill-height justify-center align-center">
                <div class="text-h2">
                    {{ slide }} Slide
                </div>
                </div>
            </v-sheet>
            </v-carousel-item>
        </v-carousel>
    </v-card-text>
    <div class="d-flex justify-end mr-4 mb-4" >
        <div 
            @click="selected_slide = slides[selectedSlideIndex == 0 ? selectedSlideIndex : selectedSlideIndex - 1]" 
            style="height: 100px; width: 100px; background-color: #FFA200"
            class="d-flex justify-start"
            >
            <v-icon 
                style="top: 50%; right: -50%; transform: translate(-50%, -50%)" 
                icon="mdi-arrow-left"
                ></v-icon>
        </div>
        <div 
            @click="selected_slide = slides[selectedSlideIndex == slides.length - 1 ? selectedSlideIndex : selectedSlideIndex + 1]" 
            style="height: 100px; width: 100px; background-color: #FFF000"
            class="d-flex justify-end"
            >
            <v-icon 
                style="top: 50%; right: 50%; transform: translate(50%, -50%)" 
                icon="mdi-arrow-right"
                ></v-icon>
        </div>
    </div>

</v-card>






<!-- <v-row>
    <v-col>
        <v-img src="@/assets/aivoorbeelden.jpg"></v-img>
    </v-col>
    <v-col>
        Voorbbeelden
    </v-col>
</v-row> -->

</template>

<script>


export default {
    name: 'VoorbeeldenPage',
    data(){
        return {
            colors: [
          'green',
          'secondary',
          'yellow darken-4',
          'red lighten-2',
          'orange darken-1',
        ],
        slides: [
          'First',
          'Second',
          'Third',
          'Fourth',
          'Fifth',
        ],
        selected_slide: 'green'

        }
    },
    computed: {
        selectedSlideIndex(){
            
            return this.slides.findIndex(e => e == this.selected_slide)
        }
    }
}
</script>

